

.bookreader{
  height: 100vh;
  background-color: #fff;
  display: flex;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;


  .userlist{
    flex:1;
    overflow-y: scroll;

    .list{
      height: 30px;
      line-height: 30px;


      .l_left{
        width: 20%;
        height: 30px;
        float: left;
      }
      .l_center{
        width: 50%;
        height: 30px;
        float: left;
      }
      .l_right{
        width: 30%;
        height: 30px;
        float: left;
      }
    }
  }
}